html {
  height: 100%; }
  html body {
    width: 100%;
    height: 100%;
    margin: 0; }

.container {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to bottom, #020107 0%, #1b4346 100%);
 }

 .container .text h1{
    font-size: 60px;
    letter-spacing: 12px;
    font-weight: 200;
    color: rgb(82, 218, 200);
 }

 .container .text p{
     font-size: 20px;
     font-weight: 100;
     word-spacing: 6px;
 }

.container::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("/src/assets/raw-logo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 400px;
  animation: fadeIn linear 1.5s;
  opacity: 0.09;
  z-index: 0;
  -webkit-animation: fadeIn linear 1.5s;
  -moz-animation: fadeIn linear 1.5s;
  -o-animation: fadeIn linear 1.5s;
  -ms-animation: fadeIn linear 1.5s;
}

.container .text {
    color: #FFF;
    /* position: absolute; */
    /* top: 50%; */
    /* right: 50%; */
    font-size: 20px;
    font-family: sans-serif;
    font-weight: bold; 
    width: 100%;
    padding: 10px;
    text-align: center;
    margin: auto;
    position: absolute;;
    top: 40%;
}

